import React from "react"
import Container from "../components/container"
import Header from "../components/header"
import Footer from "../components/footer"
import { Link } from "gatsby"

export default function Post({data}) {
  return (
        <Container>
          <Header headerTitle={data.markdownRemark.frontmatter.title} headerDescription={data.markdownRemark.frontmatter.excerpt}></Header>
          <section className="hero">
            <div className="container">
              <div className="hero-body">
                <p className="title">
                  {data.markdownRemark.frontmatter.title}
                </p>
                <p className="subtitle">
                  <span className={`${data.markdownRemark.frontmatter.type !== 'primary' ? 'hide' : ''}`} >Last update: </span>
                  <span className={`${data.markdownRemark.frontmatter.type === 'primary' ? 'hide' : ''}`} >Published on: </span>
                  {data.markdownRemark.frontmatter.date}
                </p>
                <p>
                  <Link to="/collections/">&#8249; Back to collections</Link>
                </p>
              </div>
            </div>
          </section>
          <section className="section">
            <div className="container">
              <div className="content" dangerouslySetInnerHTML={{ __html: data.markdownRemark.html }}></div>
            </div>
          </section>
          <Footer></Footer>
        </Container>
  )
}

export const query = graphql`
  query($slug: String!) {
    markdownRemark(fields: { slug: { eq: $slug } }) {
      html
      frontmatter {
        title
        date
        type
        excerpt
      }
    }
  }
`